.gradient-heart-shape {
  position: relative;
  width: 40px;
  height: 40px;
  background-image: linear-gradient(to bottom, #ef0e0e, #ca012a);
  box-shadow: 0 0 20px rgba(#000, .8);
  opacity: 0.7;
  transform: rotate(45deg);
  &::before,
  &::after {
      position: absolute;
      left: 0;
      top: 0;
      border-radius: 100%;
      width: 100%;
      height: 100%;
      content: "";
  }
  &::before {
      background-image: linear-gradient(to bottom, #EF0E0E, #CA012A);
      transform: translateX(-50%);
  }
  &::after {
      background-image: linear-gradient(to bottom, #EF0E0E 50%, transparent 50%, transparent);
      transform: translateY(-50%);
  }
}
